.wrapper{
  width: 350px;
  background: blanchedalmond;
  min-height: 400px;
  margin: 0 auto;
  margin-top: 50px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
}

form{
  display: grid;
  grid-template-columns: 4fr 1fr;
  width: 100%;
}

input[type="text"] {
  height: 40px;
  border: 0;
  border-radius: 8px;
  padding-left: 10px;
  font-size: 18px;
}

input[type="submit"] {
  border-radius: 8px;
  margin-left: 5px;
  border:0;
  background: #23e4fa;
  color: #fff;
  font-size: 17px;
}

input:focus{
  outline: none;
}

ul{
  padding: 15px;
  width: 100%;
}

li{
  list-style: none;
  font-size: 20px;
  overflow-wrap: anywhere;
  width: 285px !important;
}

.item-wrapper{
  border-top: 1px solid #000;
  display: grid;
  grid-template-columns: 6fr 1fr;
  padding: 10px;
}

button{
  border: 0;
  border-radius: 100%;
  background-color: red;
  color: #fff;
  font-weight: 800;
  height: 25px;
  width: 25px;
  justify-self: flex-end;
}